.profile_logout_ButtonMax {
    display: contents;
}

.profile_logout_ButtonMin {
    display: none;
}

.name {
    padding: 1vh;
    margin: 1vh 1vh 1vh 0vh;
    width: 100%;
    height: 11vh;
}

.email {
    padding: 1vh;
    margin: 1vh 1vh 1vh 0vh;
    width: 100%;
    height: 11vh;
}

.password {
    padding: 1vh;
    margin: 1vh 1vh 1vh 0vh;
    width: 100%;
    height: 11vh;
}

.errormsg {
    width: 80vh;
}

.buttonalign {
    display: flex;
    align-items: center;
    justify-content: center;
}

.menuDrawerMin {
    display: none;
}

#menulistmin {
    display: none;
}

.menunav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #c6c9ca;
}

.menunav ul {
    display: flex;
}

.menunav ul li {
    list-style: none;
}

.menunav ul li a {
    display: block;
    text-decoration: none;
    color: aliceblue;
    padding:0.1rem;
    margin: 0 0.5rem;
    border-radius: 0.5rem;
}

#menulistmin .active {
    background-color: blueviolet;
}

#bodycontainer {
    /* background: url('../Assets/background_1.jpg') no-repeat; */
    /* background-size: cover;
    background-position: center;
    overflow: hidden; */
    height: 100vh;
    overflow-y: scroll;
}


 @media (max-width : 480px) {

    .menunav {
        flex-direction: column;
        align-items: start;
    }

    .menunav ul {
        flex-direction: column;
        width: 100%;
        margin-bottom: 0.25rem;
    }

    .menunav ul li {
        width: 100%;
        text-align: center;
    }

    .menunav ul li a {
        margin : 0.2rem 0.5rem;
    }
    
    #update {
        width: 10vh;
        height: 3vh;
        border-radius: 30px;
    }

    #edit {
        width: 10vh;
        height: 3vh;
        border-radius: 30px;
    }

    #cancel {
        width: 10vh;
        height: 3vh;
        border-radius: 30px;
    }

    .menuDrawerMin {
        display: contents;
    }

    #menulistmin {
        display: block;
        background: #c6c9ca;
        color: white;
        padding: 1v;
    }

    .menuDrawerMax {
        display: none;
    }

    .buttonalign {
        display: flex;
        align-items: center;
        justify-content: center; 
    }

    .errormsg {
        width: 30vh;
        display: contents;
    }

    .profile_logout_ButtonMax {
        display: none;
    }

    .profile_logout_ButtonMin {
        display: contents;
    }

    .name {
        width: 100%;
        height: 9vh;
        padding: 1vh;
    }

    .email {
        width: 100%;
        height: 9vh;
        padding: 1vh;
    }

    .password {
        width: 100%;
        height: 9vh;
        padding: 1vh;
    }


}