#addproduct {
    padding: 0;
    border-radius: 30px;
    float: right;
    margin-top: -6vh;
    margin-right: 2vh;
}

#productfilter {
    padding: 1vh;
    margin-top: 1vh;
    background-color: #f0f1f2;
    width: 100%;
    margin-top: 5vh;
    display: block;
}

.productfilteralign {
    display: flex;
    margin-left: 5vh;
}

#productlist {
    margin-top: 2vh;
}

.productdetailsstockview {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.productdetailspnameview {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.productdetailsbrandview {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.brandproductnameforproductadd {
    display: flex;
    width:100%;
}

.brandnameforproductadd {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.productnameforproductadd {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.productdescforproductadd {
    padding: 1vh;
    margin: 1vh;
    width: 84vh;
    height: 11vh;
}

#productdescforproductaddinput {
    color: #9c27b0;
    width: 75vh;
    height: 4.5vh;
    border: 1px solid #1976d2;
    border-radius: 10px;
    font-size: 16px;
    padding-left: 1vh;
    margin-left: 5vh;
}

.addproductbuttonalign {
    display: flex;
    height: 5vh;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1vh;
}

.productadderrormsg {
    color: red;
    width: 35vh;
    order: 2;
}

.productaddsavecancelbutton {
    display: flex;
    order: 3;
}

#plusicon {
    font-size: 28px;
}

.buttontext {
    margin: 0vh 1vh 0vh 1vh;
}

#productbrand {
    margin-right: 2vh;
    display: flex;
}

#productproduct {
    margin-right: 2vh;
    display: flex;
}

.productbrandselect {
    width: 30vh;
}

.productproductselect {
    width: 30vh;
}

#productviewbody {
    padding-right: 2%;
}

.productdetailsview {
    width: 100%;
    display: flex;
}

#productremovebody {
    padding-right: 2%;
}

#productaddbody {
    padding-right: 2%;
}

#addproductname {
    color: #9c27b0;
    width: 80%;
    height: 4.5vh;
    border: 1px solid #1976d2;
    border-radius: 10px;
    font-size: 16px;
    padding-left: 1vh;
    margin-left: 5vh;
}

.addproductselectbrand {
    width: 90%;
}

@media (max-width : 480px) {

    .addproductselectbrand {
        width: 100%;
    }

    #addproductselectbranddiv {
        padding-right: 2vh;
    }


    .productaddbodycontext {
        padding-right: 6%;
    }

    .productremovebodycontext {
        padding-right: 6%;
    }

    .productdetailsview {
        display: contents;
    }

    .productviewbodycontext {
        padding-right: 6%;
    }

    .productbrandselect {
        width: 90%;
    }

    .productproductselect {
        width: 90%;
    }

    #productbrand {
        display: block;
    }

    #productproduct {
        display: block;
    }


    #plusicon {
        font-size: 22px;
    }

    .buttontext {
        font-size: 12px;
    }

    .productadderrormsg {
        margin-top: 1vh;
        width: 100%;
    }

    .productaddsavecancelbutton {
        margin-top: 1.5vh;
        margin-bottom: 1vh;
    }

    .addproductbuttonalign {
        display: contents;
    }

    #productdescforproductaddinput {
        width: 80%;
    }

    .productdescforproductadd {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .brandnameforproductadd {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .productnameforproductadd {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .brandproductnameforproductadd {
        display: contents;
        width: 100%;
    }

    .productdetailsstockview {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .productdetailspnameview {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .productdetailsbrandview {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    #productlist {
        margin-top: 5vh;
    }

    #clearbutton {
        margin-top: 2vh;
        width: 10vh;
        height: 3vh;
        border-radius: 30px;
        float: right;
    }

    .productfilteralign {
        display: block;
    }

    #productfilter {
        width: 100%;
        margin-top: 5vh;
    }

    #addproduct {
        padding: 0;
        border-radius: 30px;
        margin-top: 0;
        margin-right: 1vh;
    }

    .product {
        width: 100%;
    }

}