#addbrand {
    padding:0;
    border-radius: 30px;
    float: right;
    margin-top: -6vh;
    margin-right: 2vh;
}

#clearbutton {
    width: 15vh;
    height: 5vh;
    border-radius: 30px;
    float: right;
    margin-top: -5vh;
}

#brandlist {
    margin-top: 1%;
}

.brandnameforviewbrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 12vh;
}

.branddescforviewbrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 12vh;
}

.brandnameforremovebrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 12vh;
}

.branddescforremovebrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 12vh;
}

.brandnamedescforaddbrand {
    display: flex;
    width: 100%;
}

.brandnameforaddbrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

.branddescforaddbrand {
    padding: 1vh;
    margin: 1vh;
    width: 80vh;
    height: 11vh;
}

#brandnameinput {
    color: #9c27b0;
    width: 80%;
    height: 4.5vh;
    border: 1px solid #1976d2;
    border-radius: 10px;
    font-size: 16px;
    padding-left: 1vh;
    margin-left: 5vh;
}

.brandaddbuttonorder {
    display: flex;
    height: 5vh;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1vh;
}

.brandadderrormsg {
    color: red;
    width: 35vh;
    order: 2;
}

.brandsavecancelbutton {
    display: flex;
    order: 3;
}

#plusicon {
    font-size: 28px;
}

.buttontext {
    margin: 0vh 1vh 0vh 1vh;
}

#brand {
    margin-right: 2vh;
    display: flex;
}

.brandselect {
    width: 30vh;
}

#brandviewbody {
    padding-right: 2%;
}

.brandnamedescforviewbrand {
    display: flex;
    width:100%;
}

.brandnamedescforremovebrand {
    display: flex;
    width:100%;
}

#brandremovebody {
    padding-right: 2%;
}

#brandaddbody {
    padding-right: 2%;
}

@media (max-width : 480px) {

    #brandnameinput {
        width: 80%;
    }

    .brandaddbodycontext {
        padding-right: 6%;
    }

    .brandnamedescforremovebrand {
        display: contents;
        width:100%;
    }

    .brandremovebodycontext {
        padding-right: 6%;
    }

    .brandnamedescforviewbrand {
        display: contents;
        width:100%;
    }

    .brandviewbodycontext {
        padding-right: 6%;
    }

    .brandselect {
        width: 90%;
    }

    #brand {
        display: block;
    }

    #plusicon {
        font-size: 22px;
    }
    
    .buttontext {
        font-size: 12px;
    }
    
    .brandsavecancelbutton {
        margin-top: 1.5vh;
        margin-bottom: 1vh;
    }

    .brandadderrormsg {
        margin-top: .5vh;
        width: 100%;
    }

    .brandaddbuttonorder {
        display: contents;
    }

    .brandnameforaddbrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .branddescforaddbrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    .brandnamedescforaddbrand {
        display: contents;
        width: 100%;
    }

    .branddescforremovebrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 12vh;
    }

    .brandnameforremovebrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 12vh;
    }

    .branddescforviewbrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 12vh;
    }

    .brandnameforviewbrand {
        padding: 1vh;
        margin: 1vh;
        width: 100%;
        height: 10vh;
    }

    #brandlist {
        margin-top: 5vh;
    }

    #clearbutton {
        margin-top: 2vh;
        width: 10vh;
        height: 3vh;
        border-radius: 30px;
        float: right;
    }

    #brandfilter {
        display: block;
        width: 100%;
        margin-top: 5vh;
    }

    #addbrand {
        padding:0;
        border-radius: 30px; 
        margin-top: 0;
        margin-right: 1vh;
    }

    .brand {
        width: 100%;
    }
}