.customDatePicker {
  border-radius: 10px;
  border: 1px solid #1976d2;
  margin-left: 1vh;
  width: 25vh;
  height: 4.5vh;
  color: #9c27b0;
  font-size: 15px;
  padding-left: 1vh;
}


#filteralign {
  display: flex;
}

#nameemailfilter {
  display: block;
  margin-left: 5vh;
}

#datefilter {
  display: block;
  margin-left: 3vh;
}

#userlist {
  text-align: center;
  margin-top: 2vh;
  overflow-x: auto;
}

#clearbutton {
  width: 15vh;
  height: 5vh;
  border-radius: 30px;
  float: right;
  margin-top: -5vh;
}

.users #edusers {
  padding: 0;
  border-radius: 30px;
  float: right;
  margin-top: -6vh;
  margin-right: 2vh;
}

#userfilterMin {
  display: none;
}

#plusicon {
  font-size: 28px;
}

#minusicon {
  font-size: 28px;
}

.buttontext {
   margin: 0vh 1vh;
}

#pagination {
  margin: 20px auto;
}

 @media (max-width : 480px) {

  #plusicon {
    font-size: 22px;
  }
  
  #minusicon {
    font-size: 22px;
  }

  .edusersdiv {
    padding-top: 2.5vh;
  }

  .users {
    width: 100%;
  }



  .users #edusers {
    padding: 0;
    border-radius: 30px;
    margin-top: 0;
    margin-right: 1vh;
  }

  #userfilterMax {
    flex: 1;
    display: none;
  }

  #userfilterMin {
    display: block;
    width: 100%;
    margin-top: 5vh;
  }

  #userfilteralign {
    display: block;
    margin-left: 5vh;
  }



  #datefilter {
    margin-top: 1vh;
    margin-left: 0vh;
  }

  #nameemailfilter {
    margin-left: 0vh;
  }

  #clearbutton {
    margin-top: 2vh;
    width: 10vh;
    height: 3vh;
    border-radius: 30px;
    float: right;
  }

  #userlist {
    margin-top: 5vh;
  }

  .customDatePicker {
    border-radius: 10px;
    border: 1px solid #1976d2;
    margin-left: 2vh;
    width: 100%;
    height: 4.5vh;
    color: #9c27b0;
    font-size: 15px;
    padding-left: 1vh;
  }

}